/*   Community Block    */

.community-wrapper {
  position: relative;
  width: 60vw;
  height:35vw;
  margin: 20px auto;
  font-size: 16px;
  h1, h2, h3 {
    font-weight: 500;
  }
}
.community {
  position: relative;
}

#community-item-1 {
  float: left;
  width: 50%;
  color: white;
}

#community-item-2 {
  float: right;
  width: 50%;
}

.community-header {
  margin: 0px auto;
  width: 60vw;
  height: 75px;
  clear: both;
  position: relative;
  button {
    position: absolute;
    right: 10px;
    bottom: 15px;
  }
}

div.text-wrapper {
  position: absolute;
  top: 40%;
  width: 100%;
  text-shadow: 2px 2px 5px black;
}
.block-block-community-block h3, .block-block-community-block p {
  width: 100%;
  text-align: center;
  color: white;
  margin: 0px auto;
}
.big-picture {
  margin: .633% .625%;
  width: 48.75%;
  height: 65%;
  float: left;

}
.medium-picture {
  margin: .633% .625%;
  width: 48.75%;
  height: 31.66%;
  float: left;
}
.small-picture {
  margin: .633% .625%;
  width: 23.75%;
  height: 31.66%;
  float: left;
}
//.community-wrapper {
//  //background-color: #FAA600;
//}

.view-community-options .tab-pane.active {
  border: 0px solid green;
  .field-content {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    padding: 20px 0px;
    .left-side {
      margin-top: 30px;
    }
    .right-side,
    .left-side {
      flex-grow: 1;
      width: 50%;
      padding: 0px;
      p {
        margin: 0px;
        width: 100%;
        padding: 0px;
      }
      .row {
        margin: 0px 0px 20px;
        width: 100%;
        padding: 0px;
        .riv-label,
        .riv-content {
          display: block;
          width: 50%;
          padding: 0px;
        }
      }
    }
  }
}

.l-content2 {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 20px 0px 40px;
  .block {
    flex-grow: 1;
  }
  .thirds {
    width: 31%;
    margin: 1%;
  }
}

.community-block-wrapper {
  background-color: #FAA600;
  padding: 40px 0px 20px
}

